import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { DataResolveService } from './services/DataShare/data-resolve.service';

const routes: Routes = [
  {
    path: '',
    loadChildren: () => import('./tabs/tabs/tabs.module').then(m => m.TabsPageModule)
  },
  { path: 'test', loadChildren: './pages/test/test.module#TestPageModule' },
  { path: 'menu', loadChildren: './menu/menu.module#MenuPageModule' },
  { path: 'getintouch', loadChildren: './pages/get-in-touch/get-in-touch.module#GetInTouchPageModule' },
  {
     path: 'personal-informations/:id',
  resolve: {
    dataSharing: DataResolveService
  }, loadChildren: './pages/personal-informations/personal-informations.module#PersonalInformationsPageModule' },
  { path: 'medical-history', loadChildren: './pages/medical-history/medical-history.module#MedicalHistoryPageModule' },
  { path: 'request-details', loadChildren: './pages/request-details/request-details.module#RequestDetailsPageModule' },
  { path: 'service-provider', loadChildren: './pages/userService/serviceProviders/service-provider/service-provider.module#ServiceProviderPageModule' },
  { path: 'provided-services', loadChildren: './pages/userService/serviceProviders/provided-services/provided-services.module#ProvidedServicesPageModule' },
  { path: 'profile', loadChildren: './pages/userService/profile/profile.module#ProfilePageModule' },
  { path :'provided-details',loadChildren: './pages/userService/serviceProviders/provided-details/provided-details.module#ProvidedDetailsPageModule'},
  { path: 'onlin-shopping', loadChildren: './pages/onlin-shopping/onlin-shopping.module#OnlinShoppingPageModule' },
 
  { path: 'about-us', loadChildren: './pages/about-us/about-us.module#AboutUsPageModule' },
  { path: 'login', loadChildren: './login/login.module#LoginPageModule' },
  { path: 'login-verification-code/:id',
  resolve: {
    dataSharing: DataResolveService
  }, loadChildren: './login-verification-code/login-verification-code.module#LoginVerificationCodePageModule' },
  //{ path: 'lang-change', loadChildren: './components/lang-change/lang-change.module#LangChangePageModule' },
 //{ path: 'patient-lookup', loadChildren: './components/patient-lookup/patient-lookup.module#PatientLookupPageModule' },
 // { path: 'desc-terms-and-conditions-sliders', loadChildren: './components/desc-terms-and-conditions-sliders/desc-terms-and-conditions-sliders.module#DescTermsAndConditionsSlidersPageModule' },
  // { path: 'pack-up-address', loadChildren: './pages/pack-up-address/pack-up-address.module#PackUpAddressPageModule' },

];
@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule {}
