import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpRequest, HttpHandler, HttpEvent } from '@angular/common/http';
import { catchError } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';
import { Router, NavigationEnd } from '@angular/router';
import { TempDataDistributorService } from '../services/TempDataDistributor/TempDataDistributor';
import { ToastController } from '@ionic/angular';

@Injectable()
export class httpRequestInterceptor implements HttpInterceptor{

  constructor(
    private router : Router ,
    private _tempData    : TempDataDistributorService ,
    private toastController: ToastController
  
  ) { }

  intercept(req: HttpRequest<any>, next:HttpHandler): Observable<HttpEvent<any>> {
    return next.handle(req).pipe(catchError(err =>{
      if(err.status === 401 )

      this._tempData.authState.next(false);

      // this.router.events.subscribe((event) => {
      //   console.log(event);
      //   if (event instanceof NavigationEnd ) {
      //    console.log(event.url)
      //   }
      // });
     // this.presentToast();
      console.log("httpRequestInterceptor")
      return throwError(err)
    }))
  }

  async presentToast() {
    const toast = await this.toastController.create({
      message: '    ',
      closeButtonText :'',
      cssClass : "errToast",
      showCloseButton :true
    });
    toast.present();
  }
}
