import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class DataShareService {

  private data=[];
  constructor() { }

  setData( id : number | string , data : any ){
    this.data[id] = data ;
  }

  getData(id : number | string ){
    return this.data[id];
  }
}
