import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BaseUrl } from 'src/app/enums/baseUrl';
import { Observable } from 'rxjs';
import { AuthenticateModel, AuthenticateResultModel } from 'src/app/models/AuthenticateModel';
import { Request } from 'src/app/models/Request' ;


export interface MobileLoginInput{
    UserName : string | null | undefined;
    PhoneNumber : string | null | undefined;
}

export interface IMobileRegisterInput {

  name: string | null | undefined;
  surname: string | null | undefined;
  userName: string | null | undefined;
  emailAddress: string | null | undefined;
  phoneNumber: string | null | undefined;
}

export class MobileRegisterInput implements IMobileRegisterInput{
  name: string;
  surname: string;
  userName: string;
  emailAddress: string;
  phoneNumber: string;

  constructor(user? :IMobileRegisterInput ){
    this.name = user.name;
    this.surname = user.surname;
    this.userName = user.userName;
    this.emailAddress = user.emailAddress;
    this.phoneNumber = user.phoneNumber;
  }

}

export interface IMobileLoginOrRegisterOutput{
   status	: number | null | undefined;
   errorCode	: string | null | undefined;
   verificationCode: string | null | undefined;
}

export class MobileLoginOrRegisterOutput implements IMobileLoginOrRegisterOutput{
  status: number;
  errorCode: string;
  verificationCode: string;
  constructor(result? : IMobileLoginOrRegisterOutput){
    this.status = result.status;
    this.errorCode = result.errorCode;
    this.verificationCode = result.verificationCode;
  }

}

@Injectable({
  providedIn: 'root'
})
export class LoginService {

  constructor(
    private http: HttpClient
  ) { }

  loginWithNationalIdAndMobile(mobileLoginInput : MobileLoginInput): Observable<any> {
    let url_ = BaseUrl.baseUrl + "/api/services/app/Account/LoginWithNationalIdAndMobile";
    return this.http.post<any>(url_,mobileLoginInput);
  }


      /**
     * @param model (optional) 
     * @return Success
     */
    authenticate(model: AuthenticateModel | null | undefined): Observable<Request<AuthenticateResultModel>> {
        let url_ = BaseUrl.baseUrl + "/api/TokenAuth/Authenticate";
        url_ = url_.replace(/[?&]$/, "");
        return this.http.post<Request<AuthenticateResultModel>>( url_ , model);

    }

    registerMobileUser(model : IMobileRegisterInput ) : Observable<Request<IMobileLoginOrRegisterOutput>>{
      let url_ = BaseUrl.baseUrl + "/api/services/app/Account/RegisterMobileUser";
      return this.http.post<Request<IMobileLoginOrRegisterOutput>>( url_ , model);
    }

    GetCheckAuthorization() : Observable<any>{
      let url_ = BaseUrl.baseUrl + "/api/services/app/Account/GetCheckAuthorization";
      return this.http.get<any>( url_);
    }

}