import { Component, Inject } from '@angular/core';

import { Platform, ModalController } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { TranslateService } from '@ngx-translate/core';
import { DOCUMENT } from '@angular/common';
import { Storage } from '@ionic/storage';
import { Router } from '@angular/router';
import { LoadingService } from './services/loading/loading.service';
import { filesPath } from './enums/filesPath';
import { TempDataDistributorService } from './services/TempDataDistributor/TempDataDistributor';
import { LoginService } from './services/login/login';
import { pageName } from './enums/pageName';
import { LangChangeComponent } from './components/lang-change/lang-change.page';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss']
})
export class AppComponent {

  public isLogin: boolean;

  public appMenu = [
    { title: 'Request'     , url: '/tabs/tab4'      , pageName:pageName.REQUEST     ,icon: 'basket'             },
    { title: 'Profile'     , url: '/tabs/tab1'      , pageName:pageName.PROFILE     ,icon: 'person'             },
    { title: 'Chat with us', url: '/tabs/chat'      , pageName:pageName.CHAT        ,icon: 'chatboxes'          },
    { title: 'Get in touch', url: '/tabs/getintouch', pageName:pageName.GETINTOUCH  ,icon: 'list'               },
    { title: 'About'       , url: '/tabs/about-us'  , pageName:pageName.ABOUT       ,icon: 'information-circle' },
    { title: 'Lang Change' , url: 'langChange'      , pageName:'lang-change'         ,icon: 'globe'          },
    // {title:'Setting',url:'/test2',icon:'settings'},
    // {title:'Log out',url:'login',icon:'log-out'}
  ];

  accessToken: string;
  sideMenuImg: string;

  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    public _translate: TranslateService,
    private storage: Storage,
    private router: Router,
    private _loading: LoadingService,
    private _temDataDistributor: TempDataDistributorService,
    private _login: LoginService,
    @Inject(DOCUMENT) private document: Document,
    private langChange: ModalController

  ) {
    
    this.initializeApp();
  }

  initializeApp() {

    this.authState().then(data =>{
      console.log(data)
      this._temDataDistributor.authState.subscribe(isAuth =>{ 
        console.log(isAuth)
        this.isLogin = isAuth;
      })
    })
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this._initTranslate();
      this.splashScreen.hide();
    });
  }


  private _initTranslate() {

    this._temDataDistributor.profileStoredImage.subscribe(sideMenuImg => {
      this.sideMenuImg = sideMenuImg;
    });
    this.sideMenuImg = (window.localStorage.getItem("imgURL")) ? window.localStorage.getItem("imgURL") : filesPath.DEFAULT_PROFILE_IMAGE_URL;

    // Set the default language for translation strings, and the current language.

   console.log(window.localStorage.getItem('lang'))
    if (window.localStorage.getItem('lang')) {
      let default_Lang = window.localStorage.getItem('lang');
      this._translate.use(default_Lang)
      if (window.localStorage.getItem('lang') == 'ar')
        this.document.documentElement.dir = 'rtl';
      else
        this.document.documentElement.dir = 'ltr';
        console.log(window.localStorage.getItem('lang'))

    } else {
      this._translate.use('ar');
      console.log(this._translate.currentLang)
      window.localStorage.setItem('lang',this._translate.currentLang)
      if (this.platform.isRTL)
        this.document.documentElement.dir = 'ltr';
    }


    if (window.localStorage.getItem('lang') == 'ar') {
      this.appMenu[0].title = '';
      this.appMenu[1].title = '';
      this.appMenu[2].title = ' ';
      this.appMenu[3].title = '   ';
      this.appMenu[4].title = ' ';
      this.appMenu[5].title = ' ';
      // this.appMenu[5].title = '';
      // this.appMenu[5].title = '  ';
    }

    // this.accessToken = window.localStorage.getItem('accessToken');
    // console.log(this.accessToken)
    // if (!this.accessToken || this.accessToken == null) {
    //   this.appMenu[0].url = '/login';
    //   this.appMenu[1].url = '/login';
    //   this.appMenu[2].url = '/login';
    // }

    //  if(window.localStorage.getItem('accessToken')){
      this.router.navigateByUrl('tabs/tab3');
    //  }else
    //  this.router.navigateByUrl('login')


    //  if (this._translate.getBrowserLang() !== undefined) 
    //  {
    //      this._translate.use(this._translate.getBrowserLang());
    //  } 
    //  else 
    //  {
    //      this._translate.use('ar'); // Set your language here
    //  }
  }

  logOut() {
    this._loading.present();
    window.localStorage.clear();
    this._translate.use('ar');
    window.localStorage.setItem('lang', 'ar')
    this._loading.dismiss();
    this.splashScreen.show();
    window.location.reload();
    // setTimeout(() => {
    //   this.router.navigateByUrl('login')

    // }, 1500);

  }

  Login() {
    this.router.navigateByUrl('login');
  }


  authState() : Promise<any> {
    let promise = new Promise((resolve,reject)=>{
      this._login.GetCheckAuthorization().subscribe(authState => {
        console.log(authState)
        this._temDataDistributor.authState.next(true);
         resolve(true);
      }, err => {
        console.log(err)
        this._temDataDistributor.authState.next(false);
         reject(false);
      })
    });
    return promise;
  }

  openPage(pages){


    this._temDataDistributor.authState.subscribe(isAuth =>{ 
      console.log(isAuth)
      this.isLogin = isAuth;
    })

    console.log(pages)
    let page ;
    switch (pages.pageName) {
      case pageName.REQUEST:
        {
          page = (!this.isLogin)? 'login':'/tabs/tab4';
         this.router.navigateByUrl(page)
        }
        break;
      case pageName.PROFILE:
        {
          page = (!this.isLogin)? 'login':'/tabs/tab1';
          this.router.navigateByUrl(page)
        }
        break;
      case pageName.CHAT:
        {
          page = (!this.isLogin)? 'login':'/tabs/chat';
          this.router.navigateByUrl(page)
        }
      case pageName.GETINTOUCH:
        {
          page = (!this.isLogin)? 'login':'/tabs/getintouch';
          this.router.navigateByUrl(page)
        }
        break;
        case pageName.ABOUT:
          {
            this.router.navigateByUrl('/tabs/about-us')
          }
          break;
        case 'lang-change':
          {
          this.langChangeModal();
          }
          break;  
    
      default:
        break;
    }
  }

  async langChangeModal() {
    const modal = await this.langChange.create({
    component: LangChangeComponent,
    componentProps: { isFromSideMenu : true },
    cssClass:'lang-change'
    });
  
    await modal.present();
  
  }

}
