import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpRequest, HttpHandler, HttpEvent, HttpHeaders } from '@angular/common/http';
import { catchError } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';
import { Router, NavigationEnd } from '@angular/router';
import { TempDataDistributorService } from '../services/TempDataDistributor/TempDataDistributor';
import { LoginService } from '../services/login/login';

@Injectable()
export class HeaderInterceptor implements HttpInterceptor{

  constructor(
    private router : Router ,
    private _tempData    : TempDataDistributorService,
    private _login : LoginService
  
  ) { }

  /**
 * Intercept all HTTP request to add JWT token to Headers
 * @param {HttpRequest<any>} request
 * @param {HttpHandler} next
 * @returns {Observable<HttpEvent<any>>}
 * @memberof TokenInterceptor
 */
intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
  
    let token = window.localStorage.getItem('accessToken');

    console.log('==> APi Config with Token : ' + token);
    var headers_object = new HttpHeaders().set("Authorization", "bearer " + token);
    request = request.clone({
       headers : headers_object 
   });

    return next.handle(request);
  }
}
