import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { filesPath } from 'src/app/enums/filesPath';

@Injectable({
  providedIn: 'root'
})
export class TempDataDistributorService {


  imgURL : string ;
  profileStoredImage: BehaviorSubject<string>;

  authState = new BehaviorSubject(false);


  constructor() { 
    this.getStoredImage();
    this.profileStoredImage = new BehaviorSubject(this.imgURL);
  }


  getStoredImage() : string {
    
    if(this.imgURL){
        window.localStorage.setItem("imgURL" , this.imgURL);
        return this.imgURL
        }
        else
          return filesPath.DEFAULT_PROFILE_IMAGE_URL;
        }

  newImage(imgURL) {
      this.profileStoredImage.next(imgURL);
      window.localStorage.setItem("imgURL" ,imgURL);
  }

  isAuthenticated() : boolean {
    return this.authState.value;
  }



}